@import url(https://fonts.googleapis.com/css?family=Rubik&display=swap);

body {
  background-color: #0C1013;
  color: white;
  font-family: 'Rubik', sans-serif;
  letter-spacing: 1px;
  font-size: 15px; }

.app-name {
  text-align: center; }

.main-components {
  display: flex;
  flex-direction: column;
  justify-content: center; }

.snackBar {
  text-align: center;
  width: 320px;
  margin: auto !important; }

.err-snackBar {
  color: #ef5350 !important; }

.succes-snackBar {
  color: forestgreen !important; }

.closeMessageIcon {
  position: absolute;
  top: 1px;
  right: 4px; }

.note-creation-holder {
  width: 100%;
  display: flex;
  justify-content: center; }

.modal-creationPost-trigger {
  cursor: pointer; }

.title-textarea {
  height: 3em !important;
  min-height: 3em !important; }

.error {
  color: red !important;
  text-align: center;
  font-weight: 600; }

@media (max-width: 480px) {
  .modal-creationPost-trigger {
    position: fixed;
    bottom: 20px;
    z-index: 1; } }

.notes-holder {
  width: 100%;
  background-color: #0C1013 !important; }

.note-title-container {
  position: relative;
  padding-left: 20px;
  width: 100%;
  min-height: 50px;
  margin-bottom: 15px;
  padding-top: 10px;
  border-radius: 8px;
  background-color: #1A1C20; }
  .note-title-container .modal-editing-trigger {
    position: absolute;
    top: 5px;
    right: 5px;
    cursor: pointer; }

.notes-position {
  width: 50%;
  margin: auto; }

.note-title {
  font-size: 1.1em;
  font-weight: 600; }

.note-content {
  color: #9e9e9e; }

.ui.dropdown .menu > .item {
  width: 47px !important; }

@media (max-width: 480px) {
  .notes-position {
    width: 90%; } }

@media (min-width: 480px) and (max-width: 800px) {
  .notes-position {
    width: 75%; } }

